/* MAIN PARAMS*/
.round-btn {
  width: 40px;
  min-width: 40px;
  height: 40px;
  min-height: 40px;
  border: none;
  transition: 300ms; }
  .round-btn:hover {
    transform: scale(1.1);
    cursor: pointer; }

/* BTN BACKGROUNDS */
.call[hidden] {
  background: url(./static/call.svg); }
  @media (min-width: 768px) and (max-width: 1125px) {
    .call[hidden] {
      display: block; } }

.burger {
  background: url(./static/burger.svg); }

.chat[hidden] {
  background: url(./static/chat.svg); }
  @media (min-width: 768px) and (max-width: 1125px) {
    .chat[hidden] {
      display: block; } }

.profile[hidden] {
  background: url(./static/profile.svg); }
  @media (min-width: 768px) and (max-width: 1125px) {
    .profile[hidden] {
      position: relative;
      margin-right: 24px;
      display: flex; }
      .profile[hidden]::after {
        position: absolute;
        margin-left: 16px;
        position: absolute;
        content: '';
        background: #eaeaea;
        height: 32px;
        width: 2px;
        right: -20px;
        align-self: center; } }

.repair {
  background: url(./static/repair.svg); }

.status {
  background: url(./static/checkstatus.svg); }
