.blur-bg {
  display: none;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: 10;
  transition: display 300ms;
  -webkit-animation: asideBg 500ms forwards;
          animation: asideBg 500ms forwards; }

@-webkit-keyframes asideBg {
  from {
    opacity: 0; }
  to {
    opacity: 0.85; } }

@keyframes asideBg {
  from {
    opacity: 0; }
  to {
    opacity: 0.85; } }

.blur-active {
  display: flex;
  transition: display 300ms; }

.active {
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  transition: transform 350ms;
  transform: translateX(0) !important;
  box-shadow: 16px 0px 52px 0 #0e185033; }

.side-menu {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  display: flex;
  z-index: 50;
  flex-direction: column;
  padding: 24px 16px 24px 16px;
  max-width: 100%;
  min-width: 320px;
  height: 100vh;
  background: white;
  transform: translateX(-500px);
  transition: transform 350ms; }
  @media (min-width: 768px) {
    .side-menu {
      max-width: 320px; } }
  @media (min-width: 1126px) {
    .side-menu {
      display: flex;
      position: sticky;
      transform: translateX(0);
      height: 100vh; } }
  .side-menu__header {
    display: flex;
    width: 100;
    align-items: center;
    justify-content: space-between;
    gap: 16px; }
    .side-menu__header .logo {
      margin-right: auto; }
    @media (min-width: 1126px) {
      .side-menu__header .btn:nth-child(1) {
        display: none; } }
  .side-menu__navbar {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-top: 40px;
    padding-left: 4px;
    justify-content: space-between;
    overflow-y: auto;
    gap: 16px; }
  .side-menu__list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    height: auto; }
    @media (min-width: 768px) {
      .side-menu__list {
        gap: 2rem; } }
  .side-menu__list-item > a {
    display: flex;
    position: relative;
    font-family: TT Lakes Regular;
    letter-spacing: -0.2px;
    font-weight: 400;
    color: #1b1c21; }
    .side-menu__list-item > a:visited {
      color: #1b1c21; }
    .side-menu__list-item > a:hover {
      color: #7e7e82; }
    .side-menu__list-item > a:hover:before {
      position: absolute;
      content: '';
      width: 20px;
      height: 32px;
      background-color: #41f6d7;
      left: -32px;
      align-self: center;
      border-radius: 5px; }
    .side-menu__list-item > a:active:before {
      position: absolute;
      content: '';
      width: 20px;
      height: 32px;
      background-color: #41f6d7;
      left: -40px;
      align-self: center;
      border-radius: 5px; }
  .side-menu__footer {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-top: 40px; }
  .side-menu__footer-btns {
    display: flex;
    gap: 16px; }

.btn {
  min-width: 40px;
  min-height: 40px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  transition: transform 300ms 50ms; }
  .btn:hover {
    cursor: pointer;
    transform: scale(1.1); }

.btn-close {
  background-image: url(./static/close.svg); }

.btn-search {
  background-image: url(./static/search.svg); }

.btn-call {
  background-image: url(./static/call.svg); }

.btn-message {
  background-image: url(./static/chat.svg); }

.btn-profile {
  background-image: url(./static/profile.svg); }

.footer__email {
  font-size: 16px;
  color: #1b1c21; }
  .footer__email:hover {
    color: #7e7e82; }

.footer__number {
  font-size: 24px;
  line-height: 36px;
  font-family: TT Lakes Medium;
  font-weight: 700;
  color: #1b1c21; }
  .footer__number:hover {
    color: #7e7e82; }

.footer__langswitcher {
  padding-top: 40px; }

.footer__btn-lang {
  color: #1b1c21; }
  .footer__btn-lang:hover {
    color: #7e7e82; }
