.header-menu {
  display: flex;
  z-index: 2;
  width: 100vw;
  align-items: center;
  padding: 24px 16px 24px 16px;
  justify-content: space-between;
  background: #fff;
  border-bottom: 1px solid rgba(219, 255, 245, 0.719); }
  .header-menu__left {
    display: flex;
    gap: 16px;
    align-items: center; }
  .header-menu__right {
    display: flex;
    gap: 16px; }
  .header-menu__logo-wrapper {
    position: relative;
    display: flex; }
    .header-menu__logo-wrapper__logo {
      width: 96.55px;
      height: 50.81px; }
    .header-menu__logo-wrapper::after {
      margin-left: 16px;
      position: absolute;
      content: '';
      background: #eaeaea;
      height: 32px;
      width: 2px;
      left: 100%;
      align-self: center; }

/* MEDIA REQUESTS */
@media (min-width: 768px) and (max-width: 1125px) {
  .header-menu__logo-wrapper {
    display: flex;
    position: relative;
    margin-left: 16px; }
    .header-menu__logo-wrapper::after {
      display: none; }
    .header-menu__logo-wrapper::before {
      position: absolute;
      content: '';
      background: #eaeaea;
      height: 32px;
      width: 2px;
      left: -16px;
      align-self: center; } }

@media (min-width: 1126px) {
  .header-menu[hidden] {
    display: none; } }
