.modal-message {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100;
  display: flex;
  height: 100vh;
  min-width: 320px;
  width: 100vw;
  padding: 16px;
  gap: 24px;
  transform: translateX(800px);
  transition: transform 400ms;
  background-color: white; }
  .modal-message::before {
    position: absolute;
    content: '';
    width: 4px;
    height: 40px;
    background: #41f6d7;
    border-radius: 4px;
    left: 0px;
    top: 75px;
    align-self: center; }
  .modal-message h2 {
    display: flex;
    font-family: TT Lakes Medium; }
  .modal-message__form {
    display: flex;
    height: auto;
    flex-direction: column;
    min-width: 100%; }
    .modal-message__form ul {
      display: flex;
      flex-direction: column;
      gap: 16px; }
    .modal-message__form span {
      font-size: 14px;
      font-family: TT Lakes Medium;
      line-height: 16px; }
    .modal-message__form input {
      padding: 8px;
      height: 48px;
      width: 100%;
      border-radius: 8px;
      border: 1px solid #eaeaea; }
      .modal-message__form input:focus {
        outline: 2px solid #ff3e79;
        border: none; }
    .modal-message__form textarea {
      width: 100%;
      height: 118px;
      padding: 8px;
      border-radius: 8px;
      border: 1px solid #eaeaea; }
      .modal-message__form textarea:focus {
        outline: 2px solid #ff3e79;
        border: none; }
  @media (min-width: 768px) {
    .modal-message {
      max-width: 320px; } }
  @media (min-width: 1126px) {
    .modal-message {
      max-width: 460px; }
      .modal-message > button {
        position: absolute;
        left: -65px; }
      .modal-message::before {
        top: 12px; } }

.submit-btn {
  display: flex;
  width: 136px;
  height: 32px;
  color: #fff;
  font-size: 12px;
  justify-content: space-evenly;
  border-radius: 15px;
  font-family: TT Lakes Regular;
  font-weight: 400;
  align-items: center;
  background: #ff3e79;
  border: none;
  margin-left: auto; }

.modal-active {
  transform: translateX(0);
  transition: transform 500ms; }
