.section {
  display: flex;
  flex-direction: column;
  background: #f8f8f8;
  border-bottom: 1px solid #d9fff5; }
  .section__title {
    display: flex;
    line-height: 40px;
    letter-spacing: -0.6 px;
    color: #1b1c21;
    font-size: 28px;
    font-family: TT Lakes Medium; }
  .section__fullscreen-wrapper {
    display: flex;
    padding: 24px 16px 24px 16px; }
    .section__fullscreen-wrapper .title-wrapper {
      position: relative;
      display: flex; }
      .section__fullscreen-wrapper .title-wrapper::before {
        position: absolute;
        content: '';
        width: 20px;
        height: 40px;
        background: #41f6d7;
        border-radius: 4px;
        left: -32px;
        align-self: center; }
  .section__slide-menu {
    display: flex;
    overflow-x: scroll;
    padding-left: 16px;
    padding-bottom: 24px;
    padding-right: 16px;
    width: 100%; }
    .section__slide-menu__item__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1rem;
      height: 38px;
      border: 2px solid transparent;
      background: none;
      width: 238px;
      color: #1b1c21; }
      .section__slide-menu__item__btn:hover {
        border: 2px solid #b8ffec;
        border-radius: 6px;
        background: #fff; }
    .section__slide-menu::-webkit-scrollbar {
      display: none; }
  .section__article {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 0 16px 40px 16px; }
    .section__article .content-wrapper {
      display: flex;
      flex-direction: column;
      gap: 16px; }
    .section__article__img {
      display: block;
      align-self: center;
      min-width: 320px;
      width: 80%;
      border-radius: 20px; }

@media (min-width: 768px) and (max-width: 1125px) {
  .section__article {
    flex-direction: row;
    padding-bottom: 16px;
    justify-content: space-between; }
    .section__article__img {
      border-radius: 20px; } }

@media (min-width: 1126px) {
  .content-wrapper {
    width: 50%; }
    .content-wrapper__paragraph[hidden]:last-of-type {
      display: block; }
  .section__article {
    flex-direction: row;
    width: 100%;
    justify-content: space-between; }
    .section__article__img {
      width: 40%;
      border-radius: 20px; }
  .section__slide-menu {
    display: flex;
    align-items: center;
    width: auto;
    overflow: hidden;
    justify-content: space-between;
    padding-right: 16px; }
    .section__slide-menu__item__btn {
      padding-left: 5px;
      padding-right: 5px; }
  .section__fullscreen-wrapper {
    display: flex;
    align-items: center;
    gap: 16px;
    justify-content: space-between; }
    .section__fullscreen-wrapper .btn-group[hidden] {
      align-items: center;
      gap: 16px;
      display: flex;
      margin-left: auto; }
      .section__fullscreen-wrapper .btn-group[hidden] label {
        font-family: TT Lakes Medium;
        transition: color 300ms; }
        .section__fullscreen-wrapper .btn-group[hidden] label:hover {
          cursor: pointer;
          color: #ff3e79; } }
