.prices-list {
  display: flex;
  gap: 16px; }
  @media (min-width: 768px) {
    .prices-list {
      flex-direction: column;
      flex-wrap: wrap;
      gap: 0px;
      justify-content: center; } }

.prices-table-wrapper {
  padding-left: 16px;
  display: flex;
  width: 100%;
  flex-direction: column; }
  @media (min-width: 768px) {
    .prices-table-wrapper {
      padding-right: 16px; } }
  .prices-table-wrapper .prices-info {
    display: none;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 0 16px 16px 16px; }
    .prices-table-wrapper .prices-info span {
      color: #7e7e82; }
      .prices-table-wrapper .prices-info span:nth-child(1) {
        min-width: 60%; }
      .prices-table-wrapper .prices-info span:nth-child(2) {
        min-width: 15%; }
      .prices-table-wrapper .prices-info span:nth-child(3) {
        min-width: 25%; }
    @media (min-width: 768px) {
      .prices-table-wrapper .prices-info {
        display: flex; } }

.prices {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
  background: #fafafa;
  padding-bottom: 16px;
  border-bottom: 1px solid #d9fff5;
  height: auto; }
  .prices .prices-swiper {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    width: 100%; }
    @media (min-width: 768px) {
      .prices .prices-swiper {
        width: 100%; } }
  .prices__info {
    width: 100%;
    display: flex;
    font-size: 14px;
    line-height: 20px;
    color: #1b1c21; }
  .prices__commercial {
    display: flex;
    color: #1b1c21;
    font-size: 16px;
    font-family: TT Lakes Medium;
    padding-bottom: 24px; }
    .prices__commercial img {
      margin-left: 10px; }
    .prices__commercial:visited {
      color: #1b1c21; }
  .prices__title {
    display: flex;
    align-items: center;
    padding: 16px;
    font-size: 16px;
    font-family: TT Lakes Regular;
    width: 100%;
    height: 80px;
    background: linear-gradient(to right, #f8f8f8 0%, #ffffff 100%);
    text-transform: uppercase;
    font-weight: 400;
    color: #7e7e82; }
  .prices .price-slide {
    display: flex;
    width: 260px;
    height: auto; }
    @media (min-width: 768px) {
      .prices .price-slide {
        width: 100%; } }
  .prices .card {
    display: flex;
    border-radius: 6px;
    border: 1px solid #eaeaea;
    flex-direction: column;
    width: 260px;
    gap: 6px;
    background-color: #fff;
    padding: 16px; }
    @media (min-width: 768px) {
      .prices .card {
        gap: 0;
        align-items: center;
        flex-direction: row;
        flex-grow: 1;
        justify-content: space-between;
        border: none;
        border-bottom: 1px solid #eaeaea; }
        .prices .card h5 {
          display: none; } }
    .prices .card__service {
      display: flex;
      flex-direction: column; }
      @media (min-width: 768px) {
        .prices .card__service {
          width: 60%; } }
    .prices .card__price {
      display: flex;
      flex-direction: column; }
      @media (min-width: 768px) {
        .prices .card__price {
          width: 15%; } }
    .prices .card__footer {
      display: flex;
      justify-content: space-between; }
      @media (min-width: 768px) {
        .prices .card__footer {
          width: 25%; } }
      .prices .card__footer span {
        align-self: center; }
      .prices .card__footer button {
        display: flex;
        border: none;
        padding: 12px;
        border-radius: 12px;
        align-items: center;
        color: #fff;
        justify-content: space-between;
        font-family: TT Lakes Medium;
        font-weight: 400;
        width: 119px;
        height: 32px;
        background: #ff3e79;
        align-self: flex-end; }
        @media (min-width: 768px) {
          .prices .card__footer button {
            margin-left: auto;
            margin-right: 20px; } }
    .prices .card__eta {
      display: flex;
      flex-direction: column; }
      @media (min-width: 768px) {
        .prices .card__eta {
          margin-right: 25px;
          flex-direction: row; } }
