* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  box-sizing: border-box; }

*::before,
*::after {
  box-sizing: border-box;
  box-sizing: border-box; }

ul,
li {
  text-decoration: none;
  list-style: none; }

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed; }

a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  display: block; }

input,
button,
textarea,
select {
  font: inherit; }

button:hover {
  cursor: pointer; }
